nav{
    min-height: 10vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    button{
        background: transparent;
        border: none;
        cursor: pointer;
        border: 2px solid rgb(65, 65, 65);
        padding: 0.5rem 1.5rem;
        transition: all 0.5s ease;
        &:hover{
            background: rgb(65, 65, 65);
            color: white;
        }
    }
}